import React, {useState} from 'react';
import axios from 'axios';
import { useHistory } from 'react-router-dom';
const Adduser = () => {
    let History = useHistory();
    const [user, setUser] = useState({
        name: "", 
        username: "", 
        email: "", 
        address: "", 
        street: ""
    }) ;
   const { name, username, email, address, street } = user;
    const onInputChange = e => {
        setUser({...user, [e.target.name]: e.target.value});
    };
   
    const onSubmitdata = (async e => {
     e.preventDefault();
     if (user === null){
         alert("null entries are not allow");
     } else {
     try {
            const response = await axios.post("http://localhost:3003/users", user);
            History.push('/');
         } catch (e)
         {
            document.write(`Axios request failed : ${e}`);
         }}
    });
    return (
        <div className="container">
            <div className="w-75 mx-auto shadow p-5">
                <h2 className="text-center mb-4">Add User</h2>
                <form onSubmit={e => onSubmitdata(e)}>
                <div className="form-group"><input type="text" className="form-control" id="validationDefault01"
                placeholder="Enter Your name" name="name" value={name} onChange={e => onInputChange(e)}/></div>
                <div className="form-group"><input type="text" className="form-control" placeholder="Enter Your UserName" 
                    id="validationDefault02" name="username" value={username} onChange={e => onInputChange(e)}/></div>
                <div className="form-group"><input type="email" className="form-control" 
                id="exampleInputEmail1" aria-describedby="emailHelp" 
                placeholder="Enter Your E-Mail" name="email" value={email} onChange={e => onInputChange(e)}/></div>
                <div className="form-group"><input type="text" className="form-control" 
                placeholder="Enter Your Address" name="address" value={address} onChange={e => onInputChange(e)}/></div>
                <div className="form-group"><input type="text" className="form-control" 
                placeholder="Enter Your Street" name="street" value={street} onChange={e => onInputChange(e)}/></div>
                 <div className="form-group">< input type="Submit" className="btn btn-primary btn-block" value="Submit"/></div>
                </form>
            </div>
        </div>
    );
};
export default Adduser;